@keyframes spin {
  from {
    transform: rotate(0deg); }

  to {
    transform: rotate(360deg); } }

.loading {
  position: relative;
  &:before {
    background-color: rgba(255, 255, 255, 0.7);
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 11; }
  &.btn {
    &:before {
      border-radius: 20px; }
    &:disabled, &.disabled {
      opacity: 1; } }
  &:after {
    animation: spin 1300ms infinite linear;
    content: '';
    background: url(data:image/svg+xml;base64,) center center / contain no-repeat;
    height: 30px;
    width: 30px;
    margin: -15px 0 0 -15px;
    position: absolute;
    left: 50%;
    top: 50%;
    font-size: 20px;
    z-index: 12; } }
